.wrapper {
  display: flex;
  justify-content: center;
}

.detail {
  margin-top: 5%;
  padding: 1%;
  background-color: white;
  width: 50%;
  display: grid;
  justify-content: center;
  border-radius: 10px;
}

.detail img {
  position: relative;
  top: -50px;
  max-width: 350px;
  border-radius: 10px;
  box-shadow: 0 30px 60px -12px rgba(50, 50, 93, 0.25),
    0 18px 36px -18px rgba(0, 0, 0, 0.3), 0 -12px 36px -8px rgba(0, 0, 0, 0.025);
}

.detail ul {
  list-style: none;
  padding: 0;
  margin: 0;
}
